.about-me-container{
  min-height: calc(100vh - 273px);
  
  .description {
    margin-top: 45px;
    width: 750px;
    height: 275px;
    word-spacing: 5px;
    text-align: justify;
  }

}

.title {
  color: #4a4a4a;
  font-size: 28px;
  text-decoration: underline;
  margin-top: 25px;
}



.img {
  width: 400px;
  height: 400px;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 8px;
  box-shadow: 0 8px 8px 0 #773da1;
}
